import { message } from "antd";
import { PageInfoModel } from "../components/core/TableModel";

export class Fn {
    static PrintMsg(err: any | null) {
        if (err.response === undefined) {
            return err.response;
        }
        return err.response.data.message;
    };

    static PrintMsgErr(err: any | null) {
        message.error(this.PrintMsg(err));
    };

    static PrintMsgSuccess(data: any) {
        message.success(data.message);
    };

    static FormatInputMoney(data: string | null) {
        return `${data}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    }

    static ParseInputMoney(data: string) {
        return data.replace(/\s?|(,*)/g, '');
    }

    static FormatMoney(money: number) {
        return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(money);
    }

    static FormatNumber(money: number) {
        return new Intl.NumberFormat('vi-VN').format(money);
    }

    static GetIndexInArray = (datas: any, keySearch: string, value: string) => { // -1 khong tim thay
        var result = -1;
        for (var i = 0; i < datas.length; i++) {
            if (datas[i][keySearch] === value) {
                result = i;
                break;
            }
        }
        return result;
    }

    static GetIndexInArrayStr = (datas: any, value: string) => { // -1 khong tim thay
        var result = -1;
        for (var i = 0; i < datas.length; i++) {
            if (datas[i].trim() === value.trim()) {
                result = i;
                break;
            }
        }
        return result;
    }

    static SetPageInfo = (data: any, setTotal: any, setPageNumber: any, setPageSize: any) => {
        var pageInfo = data.data.data.pageInfo;
        setTotal(pageInfo.totalCount);
        setPageNumber(pageInfo.currentPage);
        setPageSize(pageInfo.pageSize);
    }

    static GetTimestamp = (data?: any, body?: any) => {
        try {
            if (body === null || body === undefined) {
                return data.data.timestamp;
            }
            body.timestamp = data.data.timestamp;
            return body;
        } catch (error) {
            console.log(error);
        }
    }

    static RenderSTT(_data: any, _row: any, index: number, pageInfo: PageInfoModel) {
        return ((pageInfo.pageNumber - 1) * pageInfo.pageSize) + index + 1;
    }



    // no use
    static PrintMsgSuccess_no_use(data: any) {
        message.success(data.data.message);
    };
    static GetTimestamp_no_use = (data?: any, body?: any) => {
        try {
            if (body === null || body === undefined) {
                return data.data.data.timestamp;
            }
            body.timestamp = data.data.data.timestamp;
            return body;
        } catch (error) {
            console.log(error);
        }
    }
}